<template>
    <div class="p-mr-4 p-mb-3" style="display: flex;">
        <p-button label="Get Report" class="p-mr-4" @click="fetchData" />
        <div class="p-field p-grid">
            <label for="date" class="p-col-fixed">Date</label>
            <div>
                <p-calendar
                    id="date"
                    v-model="selectedDate"
                    dateFormat="yy-mm-dd"
                    :showIcon="true"
                />
            </div>
        </div>
    </div>
    <p-data-table
        :value="labMonForm"
        showGridlines
        class="p-datatable-sm p-mt-1 p-shadow-1"
        :scrollable="true"
        scrollDirection="horizontal"
    >
        <template v-for="(column, index) in columns" :key="index">
            <p-column
                :field="column"
                :header="column.charAt(0).toUpperCase() + column.slice(1)"
                :style="{ width: '150px' }"
            >
                <template #body="slotProps">
                    <div class="cell-content">
                        <span
                            v-if="['name'].includes(column)"
                            class="static-text"
                        >
                            {{ slotProps.data[column].val }}
                        </span>

                        <p-input-text
                            v-else
                            :value="slotProps.data[column].val"
                            class="custom-input-text"
                            @change="
                                updateValue($event, column, slotProps.index)
                            "
                        />
                        <div
                            v-if="!['name'].includes(column)"
                            class="cell-content2"
                        >
                            <span class="static-text">{{
                                slotProps.data[column].time
                            }}</span>
                            <span class="static-text">{{
                                slotProps.data[column].initial
                            }}</span>
                        </div>
                    </div>
                </template>
            </p-column>
        </template>
    </p-data-table>
</template>

<script>
import { ref, onMounted, watch } from "vue";
import { $axios } from "@/service/app-http";

export default {
    setup() {
        const labMonForm = ref([]);
        const selectedDate = ref(new Date().toISOString().split("T")[0]); // Initialize in 'yyyy-mm-dd' format
        const columns = ref([]);

        const formatDate = (date) => {
            const d = new Date(date);
            let month = "" + (d.getMonth() + 1);
            let day = "" + d.getDate();
            const year = d.getFullYear();

            if (month.length < 2) month = "0" + month;
            if (day.length < 2) day = "0" + day;

            return [year, month, day].join("-");
        };

        const fetchData = async () => {
            try {
                const formattedDate = formatDate(selectedDate.value); // Format the date

                const response = await $axios.get("lab-mon/data", {
                    params: { date: formattedDate }
                });

                labMonForm.value = response.data;
                columns.value = Object.keys(labMonForm.value[0] || {});
            } catch (error) {
                console.error("Error fetching data:", error);
            }
        };

        const updateValue = async (event, column, rowIndex) => {
            if (rowIndex >= 0 && rowIndex < labMonForm.value.length) {
                labMonForm.value[rowIndex][column].val = event.target.value;

                const formattedDate = formatDate(selectedDate.value);

                const { data } = await $axios.post("lab-mon/save", {
                    name: labMonForm.value[rowIndex]["name"].val,
                    value: labMonForm.value[rowIndex][column].val,
                    colName: column,
                    date: formattedDate
                });

                labMonForm.value[rowIndex][column].time = data.time;
                labMonForm.value[rowIndex][column].initial = data.initial;
            } else {
                console.error(`Invalid rowIndex: ${rowIndex}`);
            }
        };

        watch(selectedDate, fetchData);

        onMounted(() => {
            fetchData();
        });

        return {
            labMonForm,
            columns,
            selectedDate,
            fetchData,
            updateValue
        };
    }
};
</script>

<style scoped>
.custom-input-text {
    background-color: transparent;
    border: none;
    width: 100%;
}

.cell-content {
    display: flex;
    flex-direction: column;
    align-items: left;
}

.cell-content2 {
    display: flex;
    justify-content: space-between;
    margin-top: 4px;
    font-size: 0.85rem;
}

.static-text {
    margin-left: 6px;
}
</style>
